// Flexbox fallback styles for browsers that don't support CSS Grid
.dcf-grid,
.dcf-grid-full,
.dcf-grid-full\@sm,
.dcf-grid-full\@md,
.dcf-grid-full\@lg,
.dcf-grid-full\@xl,
.dcf-grid-halves,
