.dcf-grid-fifths\@xl,
.dcf-grid-sixths\@md,
.dcf-grid-sixths\@lg,
.dcf-grid-sixths\@xl {
  display: flex;
  flex-wrap: wrap;
}

.dcf-grid-full > * {
  flex-basis: 100%;
}

.dcf-grid-halves > * {
  flex-basis: 50%;
}

.dcf-grid-thirds > * {
  flex-basis: 33%;
}

.dcf-grid-fourths > * {
  flex-basis: 25%;
}

.dcf-col-100\% {
  flex-basis: 100%;
}

.dcf-col-50\%-start {
  flex-basis: 50%;
}

.dcf-col-50\%-end {
  flex-basis: 50%;
}

.dcf-col-33\%-start {
  flex-basis: 33%;
}

.dcf-col-33\%-end {
  flex-basis: 33%;
}

.dcf-col-67\%-start {
  flex-basis: 67%;
}

.dcf-col-67\%-end {
  flex-basis: 67%;
}

.dcf-col-25\%-start {
  flex-basis: 25%;
}

.dcf-col-25\%-end {
  flex-basis: 25%;
}

.dcf-col-75\%-start {
  flex-basis: 75%;
}

.dcf-col-75\%-end {
  flex-basis: 75%;
}


